<template>
  <div>
    <div class="content-top">
      <img v-lazy="homeTop" alt style="width: 101.4%;margin-left: -0.6%;"/>
    </div>
    <div class="content-middle">
      <img v-lazy="homeImg2" alt/>
      <img v-lazy="homeImg3" alt/>
      <img v-lazy="homeImg4" alt/>
      <img v-lazy="homeImg5" alt/>
      <img v-lazy="homeImg6" alt/>
    </div>
    <div class="footer">
      <router-link to="/orderConfirm/life">
        <img v-lazy="homeFooter" alt/>
      </router-link>
    </div>

    <CouponModal/>
  </div>
</template>

<script>
  import CouponModal from '@/components/CouponModal';

  export default {
    components: {
      CouponModal,
    },
    data() {
      return {
        homeTop: this.$getCdnUrl("life/lifeTop.png"),
        homeImg2: this.$getCdnUrl("life/life2.png"),
        homeImg3: this.$getCdnUrl("life/life3.png"),
        homeImg4: this.$getCdnUrl("life/life4.png"),
        homeImg5: this.$getCdnUrl("life/life5.png"),
        homeImg6: this.$getCdnUrl("life/life6.png"),
        homeFooter: this.$getCdnUrl("life/lifeFooter.png"),
      };
    },
    beforeCreate() {
      document.title = '小资生活';
    },
    created() {
      // this.$wx.init('/life') // 由CouponModal触发
    },
  };
</script>

<style lang="scss" scoped>
  .content-top img {
    width: 100%;
    height: auto;
  }

  .content-middle {
    padding-bottom: 60px;
    background-color: #f0f0f0;
  }

  .content-middle img {
    width: 93%;
    margin: 4px 3.5% 0;
  }

  .footer img {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
  }
</style>



